// ==========================================
// TYPE DEFINITIONS
// ==========================================

interface WorkflowFolder {
  id: string
  name: string
  description?: string
  color?: string
  workflowCount: number
  parentFolderId?: string
}

interface Workflow {
  id: string
  name: string
  description?: string
  status: 'active' | 'inactive' | 'draft' | 'archived' | 'deleted' | 'error'
  trigger: {
    type: string
    config: Record<string, any>
  }
  folder?: {
    id: string
    name: string
    color?: string
  }
  builtBy: 'human' | 'ai'
  createdBy?: {
    id: string
    name: string
    avatar?: string
  }
  errorMessage?: string
  errorCount: number
  totalExecutions: number
  successfulExecutions: number
  failedExecutions: number
  lastExecutedAt?: string
  deletedAt?: string
  createdAt: string
  updatedAt: string
}

interface WorkflowTemplate {
  id: string
  name: string
  description: string
  category: string
  triggerType: string
  usageCount: number
  tags: string[]
  isFeatured: boolean
}

interface SmartList {
  id: string
  name: string
  filters: {
    search: string
    status: string
    triggerType: string
    builtBy: string
    folderId: string
  }
  createdAt: string
}

type TabType = 'all' | 'needs-review' | 'deleted' | 'human-built' | 'ai-built' | string // string for dynamic smart list IDs

