// ==========================================
// MAIN PAGE CONTENT
// ==========================================

function AcceleratorsContent() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const activeTab = (searchParams.get('tab') as TabType) || 'all'
  
  // State
  const [workflows, setWorkflows] = useState<Workflow[]>(MOCK_WORKFLOWS)
  const [folders, setFolders] = useState<WorkflowFolder[]>(MOCK_FOLDERS)
  const [loading, setLoading] = useState(false)
  
  // Filters & View
  const [selectedFolder, setSelectedFolder] = useState<string | null>(null)
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [triggerFilter, setTriggerFilter] = useState<string>('all')
  const [searchQuery, setSearchQuery] = useState('')
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid')
  const [sortBy, setSortBy] = useState<'name' | 'updated' | 'executions'>('updated')
  
  // Smart Lists
  const [smartLists, setSmartLists] = useState<SmartList[]>([])
  const [showSaveListModal, setShowSaveListModal] = useState(false)
  const [newListName, setNewListName] = useState('')
  
  // Modals
  const [showFolderModal, setShowFolderModal] = useState(false)
  const [editingFolder, setEditingFolder] = useState<WorkflowFolder | null>(null)

  // Load smart lists from localStorage
  useEffect(() => {
    const savedLists = localStorage.getItem('acceleratorSmartLists')
    if (savedLists) {
      setSmartLists(JSON.parse(savedLists))
    }
  }, [])

  // Check if active tab is a smart list
  const activeSmartList = useMemo(() => {
    if (activeTab.startsWith('list-')) {
      return smartLists.find(l => l.id === activeTab)
    }
    return null
  }, [activeTab, smartLists])

  // Apply smart list filters when a smart list tab is selected
  useEffect(() => {
    if (activeSmartList) {
      setSearchQuery(activeSmartList.filters.search || '')
      setStatusFilter(activeSmartList.filters.status || 'all')
      setTriggerFilter(activeSmartList.filters.triggerType || 'all')
      setSelectedFolder(activeSmartList.filters.folderId || null)
    } else if (!activeTab.startsWith('list-')) {
      // Reset filters when switching to non-smart-list tabs
      // But only if we're not on a smart list tab
    }
  }, [activeSmartList, activeTab])

  // Build dynamic tabs including smart lists
  const allTabs = useMemo(() => {
    const dynamicTabs = smartLists.map(list => ({
      id: list.id,
      label: list.name,
      icon: 'fa-filter',
      href: `/sub-account/accelerators?tab=${list.id}`,
      isSmartList: true,
    }))
    return [...BASE_TABS, ...dynamicTabs]
  }, [smartLists])

  // Filter workflows based on active tab and filters
  const filteredWorkflows = useMemo(() => {
    let result = [...workflows]

    // If on a smart list tab, apply the smart list's built-by filter first
    if (activeSmartList) {
      // Start with non-deleted workflows
      result = result.filter(w => w.status !== 'deleted')
      
      // Apply built-by filter from smart list if specified
      if (activeSmartList.filters.builtBy === 'human') {
        result = result.filter(w => w.builtBy === 'human')
      } else if (activeSmartList.filters.builtBy === 'ai') {
        result = result.filter(w => w.builtBy === 'ai')
      }
    } else {
      // Filter by static tab
      switch (activeTab) {
        case 'needs-review':
          result = result.filter(w => w.status === 'error' || w.errorCount > 0)
          break
        case 'deleted':
          result = result.filter(w => w.status === 'deleted')
          break
        case 'human-built':
          result = result.filter(w => w.builtBy === 'human' && w.status !== 'deleted')
          break
        case 'ai-built':
          result = result.filter(w => w.builtBy === 'ai' && w.status !== 'deleted')
          break
        case 'all':
        default:
          result = result.filter(w => w.status !== 'deleted')
          break
      }
    }

    // Filter by folder
    if (selectedFolder) {
      result = result.filter(w => w.folder?.id === selectedFolder)
    }

    // Filter by status
    if (statusFilter !== 'all') {
      result = result.filter(w => w.status === statusFilter)
    }

    // Filter by trigger type
    if (triggerFilter !== 'all') {
      result = result.filter(w => w.trigger.type === triggerFilter)
    }

    // Search
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      result = result.filter(w => 
        w.name.toLowerCase().includes(query) ||
        w.description?.toLowerCase().includes(query) ||
        w.trigger.type.toLowerCase().includes(query)
      )
    }

    // Sort
    result.sort((a, b) => {
      if (sortBy === 'name') return a.name.localeCompare(b.name)
      if (sortBy === 'executions') return b.totalExecutions - a.totalExecutions
      return new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime()
    })

    return result
  }, [workflows, activeTab, activeSmartList, selectedFolder, statusFilter, triggerFilter, searchQuery, sortBy])

  // Calculate stats for each tab
  const tabStats = useMemo(() => ({
    all: workflows.filter(w => w.status !== 'deleted').length,
    needsReview: workflows.filter(w => w.status === 'error' || w.errorCount > 0).length,
    deleted: workflows.filter(w => w.status === 'deleted').length,
    humanBuilt: workflows.filter(w => w.builtBy === 'human' && w.status !== 'deleted').length,
    aiBuilt: workflows.filter(w => w.builtBy === 'ai' && w.status !== 'deleted').length,
  }), [workflows])

  // Overall stats
  const stats = useMemo(() => ({
    total: workflows.filter(w => w.status !== 'deleted').length,
    active: workflows.filter(w => w.status === 'active').length,
    errors: workflows.filter(w => w.status === 'error').length,
    totalExecutions: workflows.reduce((sum, w) => sum + w.totalExecutions, 0),
    successRate: workflows.length > 0 
      ? Math.round(workflows.reduce((sum, w) => sum + (w.totalExecutions > 0 ? (w.successfulExecutions / w.totalExecutions) * 100 : 0), 0) / workflows.filter(w => w.totalExecutions > 0).length) || 0
      : 0
  }), [workflows])

  const handleCreateWorkflow = () => {
    router.push('/sub-account/accelerators/new')
  }

  const handleEditWorkflow = (workflowId: string) => {
    router.push(`/sub-account/accelerators/${workflowId}`)
  }

  const handleToggleWorkflowStatus = (workflow: Workflow) => {
    const newStatus = workflow.status === 'active' ? 'inactive' : 'active'
    setWorkflows(workflows.map(w => 
      w.id === workflow.id ? { ...w, status: newStatus } : w
    ))
  }

  const handleDeleteWorkflow = (workflowId: string) => {
    if (!confirm('Are you sure you want to delete this workflow?')) return
    setWorkflows(workflows.map(w => 
      w.id === workflowId ? { ...w, status: 'deleted', deletedAt: new Date().toISOString() } : w
    ))
  }

  const handleRestoreWorkflow = (workflowId: string) => {
    setWorkflows(workflows.map(w => 
      w.id === workflowId ? { ...w, status: 'inactive', deletedAt: undefined } : w
    ))
  }

  const handlePermanentDelete = (workflowId: string) => {
    if (!confirm('This will permanently delete the workflow. This action cannot be undone.')) return
    setWorkflows(workflows.filter(w => w.id !== workflowId))
  }

  const handleDuplicateWorkflow = (workflow: Workflow) => {
    const newWorkflow: Workflow = {
      ...workflow,
      id: `wf-${Date.now()}`,
      name: `${workflow.name} (Copy)`,
      status: 'draft',
      totalExecutions: 0,
      successfulExecutions: 0,
      failedExecutions: 0,
      errorCount: 0,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString(),
    }
    setWorkflows([newWorkflow, ...workflows])
  }

  const handleSaveSmartList = () => {
    if (!newListName.trim()) return
    
    const newList: SmartList = {
      id: `list-${Date.now()}`,
      name: newListName,
      filters: {
        search: searchQuery,
        status: statusFilter,
        triggerType: triggerFilter,
        builtBy: activeTab === 'human-built' ? 'human' : activeTab === 'ai-built' ? 'ai' : '',
        folderId: selectedFolder || '',
      },
      createdAt: new Date().toISOString(),
    }
    
    const updatedLists = [...smartLists, newList]
    setSmartLists(updatedLists)
    localStorage.setItem('acceleratorSmartLists', JSON.stringify(updatedLists))
    setShowSaveListModal(false)
    setNewListName('')
    
    // Navigate to the new smart list tab
    router.push(`/sub-account/accelerators?tab=${newList.id}`)
  }

  const handleDeleteSmartList = (listId: string) => {
    if (!confirm('Delete this smart list?')) return
    const updatedLists = smartLists.filter(l => l.id !== listId)
    setSmartLists(updatedLists)
    localStorage.setItem('acceleratorSmartLists', JSON.stringify(updatedLists))
  }

  const getTriggerInfo = (triggerType: string) => {
    return TRIGGER_LABELS[triggerType] || { 
      label: triggerType.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase()), 
      icon: 'bolt', 
      color: 'gray' 
    }
  }

  const getStatusBadge = (status: string) => {
    const styles: Record<string, string> = {
      active: 'bg-green-100 text-green-800',
      inactive: 'bg-gray-100 text-gray-800',
      draft: 'bg-yellow-100 text-yellow-800',
      error: 'bg-red-100 text-red-800',
      deleted: 'bg-red-100 text-red-800',
    }
    return styles[status] || styles.draft
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <PageHeader
        title="Accelerators"
        description="Automate your business with powerful workflows and automation"
        icon="fa-bolt"
        actions={
          <button
            onClick={handleCreateWorkflow}
            className="px-4 py-2 bg-[var(--theme-secondary)] text-white rounded-lg hover:opacity-90 flex items-center gap-2"
          >
            <i className="fas fa-plus"></i>
            New Workflow
          </button>
        }
      />

      {/* Tab Navigation */}
      <div className="border-b border-gray-200 bg-white -mx-6 px-6 overflow-x-auto">
        <nav className="-mb-px flex space-x-4" aria-label="Accelerator tabs">
          {allTabs.map((tab) => {
            const isActive = tab.id === activeTab
            const count = tab.id === 'all' ? tabStats.all :
                          tab.id === 'needs-review' ? tabStats.needsReview :
                          tab.id === 'deleted' ? tabStats.deleted :
                          tab.id === 'human-built' ? tabStats.humanBuilt :
                          tab.id === 'ai-built' ? tabStats.aiBuilt :
                          null // Smart lists don't show count
            
            return (
              <div key={tab.id} className="relative group flex items-center">
                <Link
                  href={tab.href}
                  className={`inline-flex items-center border-b-2 py-4 px-1 text-sm font-medium whitespace-nowrap transition-colors ${
                    isActive
                      ? 'border-blue-500 text-blue-600'
                      : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700'
                  }`}
                >
                  <i className={`fas ${tab.icon} mr-2 text-base ${
                    isActive ? 'text-blue-500' : 'text-gray-400 group-hover:text-gray-500'
                  }`}></i>
                  {tab.label}
                  {count !== null && (
                    <span className={`ml-2 px-2 py-0.5 rounded-full text-xs ${
                      isActive ? 'bg-blue-100 text-blue-600' : 'bg-gray-100 text-gray-500'
                    } ${tab.id === 'needs-review' && count > 0 ? 'bg-red-100 text-red-600' : ''}`}>
                      {count}
                    </span>
                  )}
                </Link>
                {/* Delete button for smart list tabs */}
                {tab.isSmartList && (
                  <button
                    onClick={(e) => {
                      e.preventDefault()
                      e.stopPropagation()
                      handleDeleteSmartList(tab.id)
                    }}
                    className="ml-1 p-1 text-gray-400 hover:text-red-500 opacity-0 group-hover:opacity-100 transition-opacity"
                    title="Delete smart list"
                  >
                    <i className="fas fa-times text-xs"></i>
                  </button>
                )}
              </div>
            )
          })}
          
          {/* New Smart List Button */}
          <button
            onClick={() => setShowSaveListModal(true)}
            className="inline-flex items-center border-b-2 border-transparent py-4 px-2 text-sm font-medium text-gray-400 hover:text-blue-600 whitespace-nowrap transition-colors"
            title="Create new Smart List from current filters"
          >
            <i className="fas fa-plus mr-1"></i>
            <span>New Smart List</span>
          </button>
        </nav>
      </div>

      {/* Stats Cards - Only show on All tab */}
      {activeTab === 'all' && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center">
                <i className="fas fa-bolt text-blue-600"></i>
              </div>
              <div>
                <p className="text-2xl font-bold text-gray-900">{stats.total}</p>
                <p className="text-sm text-gray-500">Total Workflows</p>
              </div>
            </div>
          </div>
          
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-green-100 rounded-lg flex items-center justify-center">
                <i className="fas fa-play text-green-600"></i>
              </div>
              <div>
                <p className="text-2xl font-bold text-gray-900">{stats.active}</p>
                <p className="text-sm text-gray-500">Active</p>
              </div>
            </div>
          </div>
          
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-red-100 rounded-lg flex items-center justify-center">
                <i className="fas fa-exclamation-circle text-red-600"></i>
              </div>
              <div>
                <p className="text-2xl font-bold text-gray-900">{stats.errors}</p>
                <p className="text-sm text-gray-500">Errors</p>
              </div>
            </div>
          </div>
          
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-purple-100 rounded-lg flex items-center justify-center">
                <i className="fas fa-rocket text-purple-600"></i>
              </div>
              <div>
                <p className="text-2xl font-bold text-gray-900">{stats.totalExecutions.toLocaleString()}</p>
                <p className="text-sm text-gray-500">Executions</p>
              </div>
            </div>
          </div>
          
          <div className="bg-white rounded-lg border border-gray-200 p-4">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-emerald-100 rounded-lg flex items-center justify-center">
                <i className="fas fa-check-circle text-emerald-600"></i>
              </div>
              <div>
                <p className="text-2xl font-bold text-gray-900">{stats.successRate}%</p>
                <p className="text-sm text-gray-500">Success Rate</p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Main Content Area */}
      <div className="flex gap-6">
          {/* Sidebar - Folders */}
          <div className="w-64 flex-shrink-0">
            <div className="bg-white rounded-lg border border-gray-200 p-4">
              <div className="flex items-center justify-between mb-4">
                <h3 className="font-semibold text-gray-900">Folders</h3>
                <button
                  onClick={() => { setEditingFolder(null); setShowFolderModal(true) }}
                  className="w-6 h-6 flex items-center justify-center rounded bg-gray-100 text-gray-600 hover:bg-blue-100 hover:text-blue-600 transition-colors"
                  title="Create folder"
                >
                  <i className="fas fa-plus text-xs"></i>
                </button>
              </div>
              
              <div className="space-y-1">
                <button
                  onClick={() => setSelectedFolder(null)}
                  className={`w-full text-left px-3 py-2 rounded-lg flex items-center gap-2 ${
                    selectedFolder === null ? 'bg-blue-50 text-blue-700' : 'text-gray-700 hover:bg-gray-50'
                  }`}
                >
                  <i className="fas fa-inbox text-gray-400"></i>
                  <span>All</span>
                  <span className="ml-auto text-sm text-gray-400">{filteredWorkflows.length}</span>
                </button>
                
                {folders.map(folder => (
                  <button
                    key={folder.id}
                    onClick={() => setSelectedFolder(folder.id)}
                    className={`w-full text-left px-3 py-2 rounded-lg flex items-center gap-2 ${
                      selectedFolder === folder.id ? 'bg-blue-50 text-blue-700' : 'text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    <i className="fas fa-folder" style={{ color: folder.color || '#6B7280' }}></i>
                    <span className="truncate">{folder.name}</span>
                    <span className="ml-auto text-sm text-gray-400">{folder.workflowCount}</span>
                  </button>
                ))}
              </div>
            </div>
          </div>

          {/* Main Content */}
          <div className="flex-1">
            {/* Toolbar */}
            <div className="bg-white rounded-lg border border-gray-200 p-4 mb-4">
              <div className="flex items-center gap-4 flex-wrap">
                {/* Search */}
                <div className="relative flex-1 min-w-[200px]">
                  <input
                    type="text"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    placeholder="Search workflows..."
                    className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                  <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-gray-400"></i>
                </div>
                
                {/* Status Filter */}
                <select
                  value={statusFilter}
                  onChange={(e) => setStatusFilter(e.target.value)}
                  className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">All Status</option>
                  <option value="active">Active</option>
                  <option value="inactive">Inactive</option>
                  <option value="draft">Draft</option>
                  <option value="error">Error</option>
                </select>
                
                {/* Trigger Filter */}
                <select
                  value={triggerFilter}
                  onChange={(e) => setTriggerFilter(e.target.value)}
                  className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">All Triggers</option>
                  {Object.entries(TRIGGER_LABELS).map(([key, { label }]) => (
                    <option key={key} value={key}>{label}</option>
                  ))}
                </select>
                
                {/* Sort */}
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value as any)}
                  className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                >
                  <option value="updated">Last Updated</option>
                  <option value="name">Name</option>
                  <option value="executions">Most Executions</option>
                </select>
                
                {/* View Toggle */}
                <div className="flex border border-gray-300 rounded-lg overflow-hidden">
                  <button
                    onClick={() => setViewMode('grid')}
                    className={`px-3 py-2 ${viewMode === 'grid' ? 'bg-gray-100' : 'bg-white'}`}
                  >
                    <i className="fas fa-th-large text-gray-600"></i>
                  </button>
                  <button
                    onClick={() => setViewMode('list')}
                    className={`px-3 py-2 ${viewMode === 'list' ? 'bg-gray-100' : 'bg-white'}`}
                  >
                    <i className="fas fa-list text-gray-600"></i>
                  </button>
                </div>

                {/* Save as Smart List */}
                <button
                  onClick={() => setShowSaveListModal(true)}
                  className="px-3 py-2 text-gray-600 hover:text-blue-600 hover:bg-blue-50 rounded-lg flex items-center gap-2"
                  title="Save current filters as Smart List"
                >
                  <i className="fas fa-save"></i>
                  <span className="hidden lg:inline">Save List</span>
                </button>
              </div>
            </div>

            {/* Workflows Grid/List */}
            {filteredWorkflows.length === 0 ? (
              <div className="bg-white rounded-lg border border-gray-200 p-12 text-center">
                <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <i className={`fas ${activeTab === 'needs-review' ? 'fa-check-circle text-green-400' : activeTab === 'deleted' ? 'fa-trash-alt' : 'fa-bolt'} text-gray-400 text-2xl`}></i>
                </div>
                <h3 className="text-lg font-medium text-gray-900 mb-2">
                  {activeTab === 'needs-review' ? 'All workflows are healthy!' :
                   activeTab === 'deleted' ? 'No deleted workflows' :
                   'No workflows found'}
                </h3>
                <p className="text-gray-500 mb-4">
                  {activeTab === 'needs-review' ? 'No workflows need your attention right now.' :
                   activeTab === 'deleted' ? 'Deleted workflows will appear here for 30 days.' :
                   searchQuery || statusFilter !== 'all' 
                    ? 'Try adjusting your filters' 
                    : 'Create your first workflow to automate your business'}
                </p>
                {activeTab === 'all' && !searchQuery && statusFilter === 'all' && (
                  <button
                    onClick={handleCreateWorkflow}
                    className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                  >
                    <i className="fas fa-plus mr-2"></i>
                    Create Workflow
                  </button>
                )}
              </div>
            ) : viewMode === 'grid' ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {filteredWorkflows.map(workflow => (
                  <WorkflowCard
                    key={workflow.id}
                    workflow={workflow}
                    triggerInfo={getTriggerInfo(workflow.trigger.type)}
                    statusBadge={getStatusBadge(workflow.status)}
                    isDeleted={activeTab === 'deleted'}
                    onEdit={() => handleEditWorkflow(workflow.id)}
                    onToggle={() => handleToggleWorkflowStatus(workflow)}
                    onDelete={() => handleDeleteWorkflow(workflow.id)}
                    onRestore={() => handleRestoreWorkflow(workflow.id)}
                    onPermanentDelete={() => handlePermanentDelete(workflow.id)}
                    onDuplicate={() => handleDuplicateWorkflow(workflow)}
                  />
                ))}
              </div>
            ) : (
              <WorkflowTable
                workflows={filteredWorkflows}
                getTriggerInfo={getTriggerInfo}
                getStatusBadge={getStatusBadge}
                isDeleted={activeTab === 'deleted'}
                onEdit={handleEditWorkflow}
                onToggle={handleToggleWorkflowStatus}
                onDelete={handleDeleteWorkflow}
                onRestore={handleRestoreWorkflow}
                onPermanentDelete={handlePermanentDelete}
                onDuplicate={handleDuplicateWorkflow}
              />
            )}
          </div>
        </div>

      {/* Save Smart List Modal */}
      {showSaveListModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
          <div className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4 p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-4">Save as Smart List</h3>
            <p className="text-sm text-gray-500 mb-4">
              Save your current filters as a smart list for quick access later.
            </p>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">List Name</label>
              <input
                type="text"
                value={newListName}
                onChange={(e) => setNewListName(e.target.value)}
                placeholder="e.g., Active Lead Workflows"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              />
            </div>
            <div className="text-sm text-gray-500 mb-4 p-3 bg-gray-50 rounded-lg">
              <p className="font-medium mb-1">Current Filters:</p>
              <ul className="space-y-1">
                {searchQuery && <li>Search: "{searchQuery}"</li>}
                {statusFilter !== 'all' && <li>Status: {statusFilter}</li>}
                {triggerFilter !== 'all' && <li>Trigger: {TRIGGER_LABELS[triggerFilter]?.label || triggerFilter}</li>}
                {selectedFolder && <li>Folder: {folders.find(f => f.id === selectedFolder)?.name}</li>}
                {!searchQuery && statusFilter === 'all' && triggerFilter === 'all' && !selectedFolder && (
                  <li className="italic">No filters applied</li>
                )}
              </ul>
            </div>
            <div className="flex justify-end gap-3">
              <button
                onClick={() => { setShowSaveListModal(false); setNewListName('') }}
                className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50"
              >
                Cancel
              </button>
              <button
                onClick={handleSaveSmartList}
                disabled={!newListName.trim()}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
              >
                Save List
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Folder Modal */}
      {showFolderModal && (
        <FolderModal
          folder={editingFolder}
          onClose={() => { setShowFolderModal(false); setEditingFolder(null) }}
          onSaved={(newFolder) => {
            if (editingFolder) {
              setFolders(folders.map(f => f.id === newFolder.id ? newFolder : f))
            } else {
              setFolders([...folders, newFolder])
            }
            setShowFolderModal(false)
            setEditingFolder(null)
          }}
          onDelete={(folderId) => {
            setFolders(folders.filter(f => f.id !== folderId))
            if (selectedFolder === folderId) setSelectedFolder(null)
            setShowFolderModal(false)
            setEditingFolder(null)
          }}
        />
      )}
    </div>
  )
}

// ==========================================
// WORKFLOW CARD COMPONENT
// ==========================================

function WorkflowCard({
  workflow,
  triggerInfo,
  statusBadge,
  isDeleted,
  onEdit,
  onToggle,
  onDelete,
  onRestore,
  onPermanentDelete,
  onDuplicate
}: {
  workflow: Workflow
  triggerInfo: { label: string; icon: string; color: string }
  statusBadge: string
  isDeleted: boolean
  onEdit: () => void
  onToggle: () => void
  onDelete: () => void
  onRestore: () => void
  onPermanentDelete: () => void
  onDuplicate: () => void
}) {
  const [showMenu, setShowMenu] = useState(false)

  return (
    <div className={`bg-white rounded-lg border ${workflow.status === 'error' ? 'border-red-200' : 'border-gray-200'} overflow-hidden hover:shadow-md transition-shadow`}>
      {/* Error Banner */}
      {workflow.status === 'error' && (
        <div className="bg-red-50 px-4 py-2 border-b border-red-200">
          <p className="text-sm text-red-700 flex items-center gap-2">
            <i className="fas fa-exclamation-triangle"></i>
            {workflow.errorMessage || 'Workflow has errors'}
          </p>
        </div>
      )}

      {/* Card Header */}
      <div className="p-4 border-b">
        <div className="flex items-start justify-between">
          <div className="flex-1 min-w-0">
            <button
              onClick={isDeleted ? undefined : onEdit}
              className={`font-medium text-gray-900 ${!isDeleted && 'hover:text-blue-600'} truncate block text-left`}
            >
              {workflow.name}
            </button>
            {workflow.description && (
              <p className="text-sm text-gray-500 mt-1 line-clamp-2">{workflow.description}</p>
            )}
          </div>
          <div className="relative ml-2">
            <button
              onClick={() => setShowMenu(!showMenu)}
              className="p-1 text-gray-400 hover:text-gray-600"
            >
              <i className="fas fa-ellipsis-v"></i>
            </button>
            {showMenu && (
              <>
                <div className="fixed inset-0 z-10" onClick={() => setShowMenu(false)}></div>
                <div className="absolute right-0 mt-1 w-44 bg-white rounded-lg shadow-lg border z-20">
                  {isDeleted ? (
                    <>
                      <button
                        onClick={() => { onRestore(); setShowMenu(false) }}
                        className="w-full text-left px-4 py-2 text-sm hover:bg-gray-50 flex items-center gap-2"
                      >
                        <i className="fas fa-undo text-gray-400 w-4"></i>
                        Restore
                      </button>
                      <button
                        onClick={() => { onPermanentDelete(); setShowMenu(false) }}
                        className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 flex items-center gap-2"
                      >
                        <i className="fas fa-trash w-4"></i>
                        Delete Permanently
                      </button>
                    </>
                  ) : (
                    <>
                      <button
                        onClick={() => { onEdit(); setShowMenu(false) }}
                        className="w-full text-left px-4 py-2 text-sm hover:bg-gray-50 flex items-center gap-2"
                      >
                        <i className="fas fa-edit text-gray-400 w-4"></i>
                        Edit
                      </button>
                      <button
                        onClick={() => { onDuplicate(); setShowMenu(false) }}
                        className="w-full text-left px-4 py-2 text-sm hover:bg-gray-50 flex items-center gap-2"
                      >
                        <i className="fas fa-copy text-gray-400 w-4"></i>
                        Duplicate
                      </button>
                      <button
                        onClick={() => { onToggle(); setShowMenu(false) }}
                        className="w-full text-left px-4 py-2 text-sm hover:bg-gray-50 flex items-center gap-2"
                      >
                        <i className={`fas fa-${workflow.status === 'active' ? 'pause' : 'play'} text-gray-400 w-4`}></i>
                        {workflow.status === 'active' ? 'Deactivate' : 'Activate'}
                      </button>
                      <hr className="my-1" />
                      <button
                        onClick={() => { onDelete(); setShowMenu(false) }}
                        className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 flex items-center gap-2"
                      >
                        <i className="fas fa-trash w-4"></i>
                        Delete
                      </button>
                    </>
                  )}
                </div>
              </>
            )}
          </div>
        </div>
      </div>

      {/* Trigger Badge & Builder Badge */}
      <div className="px-4 py-3 bg-gray-50">
        <div className="flex items-center gap-2">
          <div className={`w-8 h-8 rounded-lg bg-${triggerInfo.color}-100 flex items-center justify-center`}>
            <i className={`fas fa-${triggerInfo.icon} text-${triggerInfo.color}-600 text-sm`}></i>
          </div>
          <div className="flex-1 min-w-0">
            <p className="text-xs text-gray-500">Trigger</p>
            <p className="text-sm font-medium text-gray-700 truncate">{triggerInfo.label}</p>
          </div>
          <div className="flex items-center gap-2">
            <span className={`px-2 py-0.5 rounded text-xs font-medium ${
              workflow.builtBy === 'ai' ? 'bg-purple-100 text-purple-700' : 'bg-gray-100 text-gray-700'
            }`}>
              <i className={`fas ${workflow.builtBy === 'ai' ? 'fa-robot' : 'fa-user'} mr-1`}></i>
              {workflow.builtBy === 'ai' ? 'AI' : 'Human'}
            </span>
            <span className={`px-2 py-0.5 rounded text-xs font-medium ${statusBadge}`}>
              {workflow.status}
            </span>
          </div>
        </div>
      </div>

      {/* Stats */}
      <div className="px-4 py-3 grid grid-cols-3 gap-2 text-center border-t">
        <div>
          <p className="text-lg font-semibold text-gray-900">{workflow.totalExecutions}</p>
          <p className="text-xs text-gray-500">Runs</p>
        </div>
        <div>
          <p className={`text-lg font-semibold ${workflow.errorCount > 0 ? 'text-red-600' : 'text-green-600'}`}>
            {workflow.totalExecutions > 0 
              ? Math.round((workflow.successfulExecutions / workflow.totalExecutions) * 100)
              : 0}%
          </p>
          <p className="text-xs text-gray-500">Success</p>
        </div>
        <div>
          <p className="text-lg font-semibold text-gray-900">
            {workflow.lastExecutedAt 
              ? new Date(workflow.lastExecutedAt).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })
              : '-'}
          </p>
          <p className="text-xs text-gray-500">{isDeleted ? 'Deleted' : 'Last Run'}</p>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// WORKFLOW TABLE COMPONENT
// ==========================================

function WorkflowTable({
  workflows,
  getTriggerInfo,
  getStatusBadge,
  isDeleted,
  onEdit,
  onToggle,
  onDelete,
  onRestore,
  onPermanentDelete,
  onDuplicate
}: {
  workflows: Workflow[]
  getTriggerInfo: (type: string) => { label: string; icon: string; color: string }
  getStatusBadge: (status: string) => string
  isDeleted: boolean
  onEdit: (id: string) => void
  onToggle: (workflow: Workflow) => void
  onDelete: (id: string) => void
  onRestore: (id: string) => void
  onPermanentDelete: (id: string) => void
  onDuplicate: (workflow: Workflow) => void
}) {
  return (
    <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
      <table className="w-full">
        <thead className="bg-gray-50 border-b">
          <tr>
            <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Name</th>
            <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Trigger</th>
            <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Built By</th>
            <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Status</th>
            <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">Executions</th>
            <th className="text-left px-4 py-3 text-sm font-medium text-gray-600">{isDeleted ? 'Deleted' : 'Last Run'}</th>
            <th className="text-right px-4 py-3 text-sm font-medium text-gray-600">Actions</th>
          </tr>
        </thead>
        <tbody className="divide-y">
          {workflows.map(workflow => {
            const triggerInfo = getTriggerInfo(workflow.trigger.type)
            return (
              <tr key={workflow.id} className={`hover:bg-gray-50 ${workflow.status === 'error' ? 'bg-red-50' : ''}`}>
                <td className="px-4 py-3">
                  <button 
                    onClick={() => !isDeleted && onEdit(workflow.id)}
                    className={`font-medium text-gray-900 ${!isDeleted && 'hover:text-blue-600'}`}
                  >
                    {workflow.name}
                  </button>
                  {workflow.description && (
                    <p className="text-sm text-gray-500 truncate max-w-xs">{workflow.description}</p>
                  )}
                  {workflow.errorMessage && (
                    <p className="text-xs text-red-600 mt-1">{workflow.errorMessage}</p>
                  )}
                </td>
                <td className="px-4 py-3">
                  <span className="flex items-center gap-2 text-sm text-gray-600">
                    <i className={`fas fa-${triggerInfo.icon} text-${triggerInfo.color}-500`}></i>
                    {triggerInfo.label}
                  </span>
                </td>
                <td className="px-4 py-3">
                  <span className={`px-2 py-1 rounded text-xs font-medium ${
                    workflow.builtBy === 'ai' ? 'bg-purple-100 text-purple-700' : 'bg-gray-100 text-gray-700'
                  }`}>
                    <i className={`fas ${workflow.builtBy === 'ai' ? 'fa-robot' : 'fa-user'} mr-1`}></i>
                    {workflow.builtBy === 'ai' ? 'AI Built' : 'Human Built'}
                  </span>
                </td>
                <td className="px-4 py-3">
                  <span className={`px-2 py-1 rounded text-xs font-medium ${getStatusBadge(workflow.status)}`}>
                    {workflow.status}
                  </span>
                </td>
                <td className="px-4 py-3 text-sm text-gray-600">
                  {workflow.totalExecutions.toLocaleString()}
                  {workflow.totalExecutions > 0 && (
                    <span className={workflow.errorCount > 0 ? 'text-red-600 ml-1' : 'text-green-600 ml-1'}>
                      ({Math.round((workflow.successfulExecutions / workflow.totalExecutions) * 100)}%)
                    </span>
                  )}
                </td>
                <td className="px-4 py-3 text-sm text-gray-500">
                  {isDeleted 
                    ? workflow.deletedAt ? new Date(workflow.deletedAt).toLocaleDateString() : '-'
                    : workflow.lastExecutedAt 
                      ? new Date(workflow.lastExecutedAt).toLocaleDateString()
                      : 'Never'}
                </td>
                <td className="px-4 py-3 text-right">
                  <div className="flex items-center justify-end gap-2">
                    {isDeleted ? (
                      <>
                        <button
                          onClick={() => onRestore(workflow.id)}
                          className="p-2 rounded hover:bg-gray-100 text-blue-600"
                          title="Restore"
                        >
                          <i className="fas fa-undo"></i>
                        </button>
                        <button
                          onClick={() => onPermanentDelete(workflow.id)}
                          className="p-2 rounded hover:bg-gray-100 text-red-600"
                          title="Delete Permanently"
                        >
                          <i className="fas fa-trash"></i>
                        </button>
                      </>
                    ) : (
                      <>
                        <button
                          onClick={() => onToggle(workflow)}
                          className={`p-2 rounded hover:bg-gray-100 ${
                            workflow.status === 'active' ? 'text-green-600' : 'text-gray-400'
                          }`}
                          title={workflow.status === 'active' ? 'Deactivate' : 'Activate'}
                        >
                          <i className={`fas fa-${workflow.status === 'active' ? 'pause' : 'play'}`}></i>
                        </button>
                        <button
                          onClick={() => onEdit(workflow.id)}
                          className="p-2 rounded hover:bg-gray-100 text-gray-600"
                          title="Edit"
                        >
                          <i className="fas fa-edit"></i>
                        </button>
                        <button
                          onClick={() => onDuplicate(workflow)}
                          className="p-2 rounded hover:bg-gray-100 text-gray-600"
                          title="Duplicate"
                        >
                          <i className="fas fa-copy"></i>
                        </button>
                        <button
                          onClick={() => onDelete(workflow.id)}
                          className="p-2 rounded hover:bg-gray-100 text-red-600"
                          title="Delete"
                        >
                          <i className="fas fa-trash"></i>
                        </button>
                      </>
                    )}
                  </div>
                </td>
              </tr>
            )
          })}
        </tbody>
      </table>
    </div>
  )
}

// ==========================================
// FOLDER MODAL COMPONENT
// ==========================================

function FolderModal({
  folder,
  onClose,
  onSaved,
  onDelete
}: {
  folder: WorkflowFolder | null
  onClose: () => void
  onSaved: (folder: WorkflowFolder) => void
  onDelete: (id: string) => void
}) {
  const [name, setName] = useState(folder?.name || '')
  const [color, setColor] = useState(folder?.color || '#3B82F6')

  const colors = ['#3B82F6', '#10B981', '#F59E0B', '#EF4444', '#8B5CF6', '#EC4899', '#6B7280']

  const handleSave = () => {
    if (!name.trim()) return
    
    const newFolder: WorkflowFolder = {
      id: folder?.id || `folder-${Date.now()}`,
      name,
      color,
      workflowCount: folder?.workflowCount || 0,
    }
    
    onSaved(newFolder)
  }

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4 p-6">
        <h3 className="text-lg font-bold text-gray-900 mb-4">
          {folder ? 'Edit Folder' : 'Create Folder'}
        </h3>

        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Folder Name</label>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              placeholder="e.g., Lead Nurturing"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Color</label>
            <div className="flex gap-2">
              {colors.map(c => (
                <button
                  key={c}
                  onClick={() => setColor(c)}
                  className={`w-8 h-8 rounded-full ${color === c ? 'ring-2 ring-offset-2 ring-blue-500' : ''}`}
                  style={{ backgroundColor: c }}
                />
              ))}
            </div>
          </div>
        </div>

        <div className="flex items-center justify-between mt-6">
          {folder ? (
            <button
              onClick={() => onDelete(folder.id)}
              className="text-red-600 hover:text-red-700 text-sm"
            >
              Delete Folder
            </button>
          ) : (
            <div></div>
          )}
          <div className="flex gap-3">
            <button
              onClick={onClose}
              className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50"
            >
              Cancel
            </button>
            <button
              onClick={handleSave}
              disabled={!name.trim()}
              className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50"
            >
              {folder ? 'Save Changes' : 'Create Folder'}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

